import React, { useState, useEffect } from 'react';
import { Modal, Input, Button } from 'antd';
import { useTranslation } from 'react-i18next';

//  props 
interface EditTagsDialogProps {
    visible: boolean;
    onClose: () => void;
    onEdit: (id: number, updatedTag: Tag) => void;
    tag: {
        id: number;
        name: string;
    };
}
// Tag 
interface Tag {
    id: number;
    name: string;
}

const EditTagsDialog: React.FC<EditTagsDialogProps> = ({ visible, onClose, onEdit, tag }) => {
    const [tagName, setTagName] = useState<string>('');
    const { t } = useTranslation();

    useEffect(() => {
        if (visible && tag) {
            setTagName(tag.name);
        }
    }, [visible, tag]);

    const handleEdit = () => {
        if (!tagName.trim()) {
            alert(t('validation.tagNameRequired'));
            return;
        }
        onEdit(tag.id, { id: tag.id, name: tagName });
        onClose();
    };

    return (
        <Modal
            title={t('editTagDialog.title')} //  t 
            open={visible}
            onCancel={onClose}
            maskClosable={false}
            footer={[
                <Button key="back" onClick={onClose}>{t('common.cancel')}</Button>, //  t 
                <Button key="submit" type="primary" onClick={handleEdit}>{t('editTagDialog.saveChanges')}</Button>, //  t 
            ]}
        >
            <Input
                placeholder={t('editTagDialog.tagNamePlaceholder')} //  t 
                value={tagName}
                onChange={e => setTagName(e.target.value)}
            />
        </Modal>
    );
};

export default EditTagsDialog;
