import React from 'react';
import ReactDOM from 'react-dom/client';
import './index.css';
import App from './App';
import reportWebVitals from './reportWebVitals';
import axios from 'axios';
import i18n from 'i18next';
import { initReactI18next } from 'react-i18next';
import enTranslation from './assets/language/en.json';
import zhTranslation from './assets/language/zh.json';

async function initializeApp() {
  try {
    // 
    const response = await axios.get<{ language: string }>('http://localhost:3001/language');
    const currentLanguage = response.data.language || 'zh'; // 'zh'

    //  i18next
    i18n
      .use(initReactI18next)
      .init({
        resources: {
          en: { translation: enTranslation },
          zh: { translation: zhTranslation }
        },
        lng: currentLanguage, // 
        fallbackLng: 'en',
        interpolation: { escapeValue: false }
      });

    // 
    const rootElement = document.getElementById('root');
    if (!rootElement) throw new Error("Failed to find the root element");
    const root = ReactDOM.createRoot(rootElement);
    root.render(
      <React.StrictMode>
        <App />
      </React.StrictMode>
    );
  } catch (error) {
    console.error('Error initializing app:', error);
  }
}

// 
initializeApp();

// Performance measuring
reportWebVitals();
