import React, { useState } from 'react';
import './App.css';
import Navbar from './components/NavBar';
import DataTable from './components/DataTable';
import TagsTable from './components/TagsTable';
import Sidebar from './components/Sidebar'; //  Sidebar  TypeScript

const App: React.FC = () => {
  const [activeComponent, setActiveComponent] = useState<string>('data'); //  string 

  const handleMenuSelect = (menu: string) => {
    setActiveComponent(menu); //  menu  string 
  };

  return (
    <div className="App">
      <Sidebar onMenuSelect={handleMenuSelect} />
      <Navbar />
      <div className="main-content">
        {activeComponent === 'data' ? <DataTable /> : <TagsTable />}
      </div>
    </div>
  );
};

export default App;
