import React, { useState } from 'react';
import { Modal, Input, Button } from 'antd';
import axios from 'axios';
import { useTranslation } from 'react-i18next';

//  props 
interface AddTagsDialogProps {
    visible: boolean;
    onClose: () => void;
    onAdd: (newTag: { id: number; name: string }) => void;
}

const AddTagsDialog: React.FC<AddTagsDialogProps> = ({ visible, onClose, onAdd }) => {
    const [tagName, setTagName] = useState<string>('');
    const { t } = useTranslation();

    const handleAdd = async () => {
        if (!tagName.trim()) {
            alert(t('validation.tagNameRequired'));
            return;
        }
        try {
            const response = await axios.post('http://localhost:3001/tags', { name: tagName });
            if (response.status === 200) {
                // ID
                const newTag = { id: response.data.id, name: tagName };
                onAdd(newTag); // 
                setTagName(''); // 
                onClose(); // 
            }
        } catch (error) {
            console.error('Error adding tag:', error);
        }
    };

    return (
        <Modal
            title={t('addTagsDialog.title')} //  t 
            open={visible}
            maskClosable={false}
            onCancel={() => {
                setTagName('');
                onClose();
            }}
            footer={[
                <Button key="back" onClick={() => {
                    setTagName('');
                    onClose();
                }}>{t('common.cancel')}</Button>, //  t 
                <Button key="submit" type="primary" onClick={handleAdd}>{t('common.add')}</Button>, //  t 
            ]}
        >
            <Input
                placeholder={t('addTagsDialog.placeholder')} //  t 
                value={tagName}
                onChange={e => setTagName(e.target.value)}
                onPressEnter={handleAdd} // 
            />
        </Modal>
    );
};

export default AddTagsDialog;
