import React, { useState, useEffect } from 'react';
import { Modal, Input, Select, Tag, Button } from 'antd';
import axios from 'axios';
import { useTranslation } from 'react-i18next';

//  props 
interface AddDataDialogProps {
    visible: boolean;
    onClose: () => void;
    onAdd: (data: { name: string; description: string; time: string; tags: string }) => void;
}

const AddDataDialog: React.FC<AddDataDialogProps> = ({ visible, onClose, onAdd }) => {
    const [name, setName] = useState<string>('');
    const [description, setDescription] = useState<string>('');
    const [tags, setTags] = useState<string[]>([]); // 
    const [selectedTags, setSelectedTags] = useState<string[]>([]); // 
    const { t } = useTranslation();

    useEffect(() => {
        const fetchTags = async () => {
            try {
                const response = await axios.get('http://localhost:3001/tags');
                if (response.status === 200) {
                    setTags(response.data.map((tag: { name: string }) => tag.name)); // 
                }
            } catch (error) {
                console.error('Error fetching tags:', error);
            }
        };
        fetchTags();
    }, []);

    const handleAdd = () => {
        // 
        if (!name.trim()) {
            alert(t('validation.nameRequired'));
            return;
        }

        if (selectedTags.length === 0) {
            alert(t('validation.tagRequired'));
            return;
        }
        // 
        const now = new Date();
        const utcTime = now.getTime() + (now.getTimezoneOffset() * 60000); // UTC
        const cstTime = new Date(utcTime + (3600000 * 8)); //  (UTC+8)

        // 
        const formattedDate = cstTime.getFullYear() + '-' +
            ('0' + (cstTime.getMonth() + 1)).slice(-2) + '-' +
            ('0' + cstTime.getDate()).slice(-2);

        const data = {
            name,
            description,
            time: formattedDate,
            tags: selectedTags.join(',')
        };
        onAdd(data);
        handleClose();
    };

    const handleTagRemove = (tag: string) => {
        setSelectedTags(prevTags => prevTags.filter(t => t !== tag));
    };

    const handleClose = () => {
        setName('');
        setDescription('');
        setSelectedTags([]);
        onClose();
    };

    return (
        <Modal
            title={t('addDataDialog.title')} //  t 
            open={visible}
            maskClosable={false}
            onCancel={handleClose}
            footer={[
                <Button key="back" onClick={handleClose}>{t('common.cancel')}</Button>, //  t 
                <Button key="submit" type="primary" onClick={handleAdd}>{t('common.submit')}</Button>, //  t 
            ]}
        >
            <Input
                placeholder={t('addDataDialog.namePlaceholder')} //  t 
                value={name}
                onChange={e => setName(e.target.value)}
                style={{ marginBottom: 8 }}
            />
            <Input.TextArea
                placeholder={t('addDataDialog.descriptionPlaceholder')} //  t 
                value={description}
                onChange={e => setDescription(e.target.value)}
                style={{ marginBottom: 8 }}
            />
            <Select
                mode="multiple"
                placeholder={t('addDataDialog.selectTags')} //  t 
                value={selectedTags}
                onChange={setSelectedTags}
                style={{ width: '100%', marginBottom: 8 }}
            >
                {tags.map(tag => (
                    <Select.Option key={tag} value={tag}>{tag}</Select.Option>
                ))}
            </Select>
            <div>
                {selectedTags.map(tag => (
                    <Tag
                        closable
                        onClose={() => handleTagRemove(tag)}
                        key={tag}
                        style={{ marginBottom: 4 }}
                    >
                        {tag}
                    </Tag>
                ))}
            </div>
        </Modal>
    );
};

export default AddDataDialog;
